html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  margin: 0px auto;
  width: 100%;
  height: 100%;
  background-color: #527779;
  font-family: "Oswald", sans-serif;
  overflow-y: hidden;
  display: flex;
}

/** Media Queries **/
.nav {
  background-color: #181818;
  width: 30rem;
  height: 100%;
  padding-top: 1rem;
  box-shadow: 7px 0px 15px 1px rgba(0, 0, 0, 0.75);
}
.nav .titulo {
  text-align: center;
  color: rgb(68, 141, 181);
  font-size: 3.5rem;
  margin-bottom: 0;
  font-family: "Oswald", sans-serif;
}
.nav .contenedor-superior {
  display: flex;
  justify-content: center;
  width: auto;
  height: 60%;
  flex-direction: column;
  margin: 0 2.5rem;
  border-bottom: 1px solid #FFFFFF;
}
.nav .contenedor-superior .links {
  font-size: 3rem;
  color: #FFFFFF;
  margin: 0.3rem;
  padding: 2rem 0;
}
.nav .contenedor-inferior {
  display: flex;
  justify-content: center;
  width: auto;
  height: 20%;
  flex-direction: column;
  margin: 0 2.5rem;
}
.nav .contenedor-inferior .links2 {
  font-size: 3rem;
  color: #FFFFFF;
  margin: 0.3rem;
  padding: 2rem 0;
}

.negro {
  display: grid;
  place-items: center;
  background-color: rgb(68, 141, 181);
  border: 1px solid black;
}

.blanco {
  display: grid;
  place-items: center;
  background-color: #FFFFFF;
  border: 1px solid black;
}

.chess {
  display: grid;
  border: 1px solid #181818;
  height: 75rem;
  width: 75rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
}

.plays {
  height: 100%;
  width: 30rem;
}
.plays .party {
  border: 1px solid black;
  background-color: #181818;
  height: 90rem;
  width: 100%;
}
.plays .party h2 {
  text-align: center;
  font-size: 2rem;
  color: white;
}

.piezas {
  width: 5rem;
  border-radius: 50%;
  margin: 0px auto;
  height: 5rem;
}

.white {
  background-color: #e1e1e1;
}

.black {
  background-color: #181818;
}